<template>
    <div ref="canvasContainer" class="canvas-container"></div>
  </template>
  
  <script>
  import * as THREE from 'three';
  
  export default {
    name: 'AppSphere',
    mounted() {
      this.createWireframeSphere();
    },
    methods: {
      createWireframeSphere() {
        const scene = new THREE.Scene();
        const camera = new THREE.PerspectiveCamera(75, 500 / 500, 0.1, 1000);
        
        // Definindo o renderer com fundo transparente
        const renderer = new THREE.WebGLRenderer({ alpha: true });
        renderer.setSize(500, 500);
        this.$refs.canvasContainer.appendChild(renderer.domElement);
  
        // Criando a geometria da esfera (aumentando o raio de 5 para 8)
        const geometry = new THREE.SphereGeometry(8, 32, 32);
  
        // Criando o wireframe (esfera oca com polígonos)
        const wireframe = new THREE.WireframeGeometry(geometry);
        const lineMaterial = new THREE.LineBasicMaterial({ color: 0x6A0DAD }); // Cor roxa
        const wireframeMesh = new THREE.LineSegments(wireframe, lineMaterial);
        
        // Inclinando a esfera (30 graus no eixo X)
        wireframeMesh.rotation.x = Math.PI / 20;
        
        scene.add(wireframeMesh);
  
        // Luz
        const light = new THREE.PointLight(0xffffff, 1);
        light.position.set(10, 10, 10);
        scene.add(light);
  
        camera.position.z = 15; // Afastando um pouco mais a câmera devido ao aumento da esfera
  
        // Animação
        const animate = () => {
          requestAnimationFrame(animate);
          wireframeMesh.rotation.y += 0.003; // Animação lenta
          renderer.render(scene, camera);
        };
  
        animate();
      }
    }
  }
  </script>
  
  <style>
  .canvas-container {
    width: 500px;
    height: 100%;
    display: flex;
    align-items: center;
    background-color: rgba(0, 0, 0, 0); 
  }
  </style>
  